* {
  padding: 7px ;
  margin: 0;
  box-sizing: border-box;
}
body {
  background: url("../img/pattern10.png");
}
form {

    .form-group {
      display: flex;
      flex-direction: column;
      justify-content: center;
      label {
        font-size: 24px;
        font-weight: 500 ;
        display: block;
      }
      textarea {
        &#response {
          font-size: 1.5rem;
          color: #ea0909;
        }
      }
    }
}